@import "keyboard";
@import "tooltips";

#fork{
	position: absolute; 
	top: 0; 
	right: 0;
	z-index:99;
}

.icon-help{
	width:40px;
	height:40px;
}

.help{
	
	position:fixed;
	top:0;
	left:0;
	width:2em;
	&:hover{
		width:100%;
	}
	
	a.showHelp {
		
		color:@white;
		display:block;
		position:relative;
		margin:6px 0 0 6px;
		width:44px;
		height:44px;
		text-decoration:none;
		background-image:url("../img/svg/help-a.svg");
		background-size:0;
		//
		color:transparent;
		
		&:before{
			content:' ';
			background-image:url("../img/svg/help-b.svg");
			background-repeat:no-repeat;
			background-size:cover;
			width:100%;
			height:100%;
			display:block;
			.transition(e("background 0.5s ease-out, rotation 0.5s ease-out"));
			
		}	
		
		#keyboard{
			visibility:hidden;
			display:none;
			position:absolute;
			top:0;
			left:50%;
			margin-left:( @keyboard-width / -2 );
			margin-top:6px;
			z-index:2;
		}
		
		&:hover{
			width:100%;
			height:@keyboard-height;
			text-decoration:none;
			
			#keyboard{
				visibility:visible;//
				display:block;
				.animation( fadeIn 0.2s 1 ease forwards );
			}
			
			&:before{
				content:" ";
				background-image:url("../img/svg/help-a.svg");
				width:44px;
				height:44px;
				//.rotate(45deg);
			}
		}
	}
	.instructions{
		visibility:hidden;
		display:none;
		width:@keyboard-width;
		position:absolute;
		top:@keyboard-height;
		left:50%;
		margin-left:( @keyboard-width / -2 );
		margin-top:( 12px + 12px );
		color:@white;
		.gradient-vertical( rgba( 50,50,105,1), rgba(10,10,105,1) );
		.border-radius( 10px );
		border:1px solid #f5aca6;
		h2,h3,h4,h5,h6,p{
			padding:0 12px;
		}
		h4{
			.visuallyhidden();
		}
		small{
			.ir();
			width:24px;
			height:24px;
			position:absolute;
			top:6px;
			right:6px;
		}
	}
	
	a.showHelp:hover + .instructions{
		visibility:visible;//
		display:block;
		.animation( fadeIn 1s 1 ease forwards );
	}
	
}
.live{
	.help h3{
		.visuallyhidden();
	}
	#soundwheel{
		background-color: rgba(36,22,30,1);
		opacity:1;
	
	}
	header{
		visibility:hidden;
	}
}


::-moz-selection { background: @white; color: @black; text-shadow: none; }
::selection { background: @white; color: @black; text-shadow: none; }